<template>
    <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>

<script>
    import echarts from 'echarts';
    export default {
        props: {
            className: {
                type: String,
                default: 'scatterChart'
            },
            id: {
                type: String,
                default: 'scatterChart'
            },
            width: {
                type: String,
                default: '100%'
            },
            height: {
                type: String,
                default: '100%'
            }
        },
        data() {
            return {
                scatterChart: null
            };
        },
        mounted() {
            //this.initChart();
            this.scatterChart = null;
            this.scatterChart = echarts.init(document.getElementById(this.id));
        },
        methods: {
            initChart(hours,days,data) {
                // data = data.map(function (item) {
                //     return [item[1], item[0], item[2]];
                // });
               data = data.map(item=>{
                     item = item.map(subItem=>{
                            return  [
                                  hours[subItem[1]],
                                  days[subItem[0]],
                                  subItem[2]
                            ]
                    })
                    return item
                })
                this.scatterChart.setOption({
                    tooltip: {
                        position: 'top',
                        formatter: function (params) {
                            return params.value[0]+'&nbsp;'+params.value[1]+'&nbsp;'+params.value[2];
                        }
                    },
                    grid: {
                        top:'8%',
                        left:'3%',
                        bottom: '5%',
                        right: '8%',
                        containLabel: '5%',
                    },
                    xAxis: {
                        type: 'category',
                        name:'Strength',
                        nameGap:20,
                        data: hours,
                        boundaryGap: false,
                        axisLabel: {
                            color   : 'rgba(184,226,255,1)',
                        },
                        axisTick:{
                            show:false,
                        },
                        axisLine: {
                            show:false,
                        }
                    },
                    yAxis: {
                        type: 'category',
                        data: days,
                        splitLine: {
                            show     : true,
                            lineStyle: {
                                color: 'rgba(0,30,69,1)',
                                type : 'dashed'
                            }
                        },
                        splitNumber:8,
                        axisTick:{//y轴刻度线
                            show:false,
                            length:5
                        },
                        axisLabel: {
                            color:'rgba(184,226,255,.5)',
                            fontSize:16,
                            rich: {
                                a: {
                                    color: 'rgba(184,226,255)',
                                    fontSize:16,
                                }
                            },
                            // formatter: '{a|value}'
                            formatter: function (value) {
                               return  value
                            }
                        },
                        axisLine: {
                            show:false,
                        }
                    },
                    series: [{
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[0],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    },
                    {
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[1],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    },
                    {
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[2],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    },
                    {
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[3],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    },
                    {
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[4],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    },
                    {
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[5],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    },
                    {
                        type: 'scatter',
                        itemStyle: {
                                color:'rgba(35,197,255,.2)',
                            },
                        symbolSize: function (val) {
                            return Math.sqrt(val[2])*0.8//val[2]*2;
                        },
                        data: data[6],
                        animation:true,
                       /* animationDelay: function (idx) {
                            return idx * 5;
                        },*/
                        animationDurationUpdate: 1000,
                        animationEasingUpdate: 'quinticInOut',
                    }
                    ]
                })
                window.onresize  = this.scatterChart.resize
            },
            resizeChart(data){
                var options = this.scatterChart.getOption();
                options.series = options.series.map((item,index)=>{
                    return item.data = data[index]
                })
                this.scatterChart.setOption(options)
            }
        }
    }
</script>
